import Joi from 'joi';

const badge = {
	create: {
		name: Joi.string().min(2).required(),
		file: Joi.required(),
		action: Joi.any().valid(['exams', 'forum', '']).required(),
		criteria: Joi.string().required(),
		value: Joi.number().required()
	},
	update: {
		name: Joi.string().min(2),
		avatar: Joi.required(),
		action: Joi.any().valid(['exams', 'forum', '']),
		criteria: Joi.string(),
		value: Joi.number()
	}
};

export default badge;
