import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
* Fetches badges
* @returns {Promise} response - a promise of a categories arrays
*/
export function getBadges(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/badges?limit=${limit}&offset=${offset}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches a badge
* @param {String} id - id of badge
* @returns {Promise} response - a promise of a deleted badge item
*/
export function getBadge(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/badges/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches a badge
* @param {String} id - id of badge
* @returns {Promise} response - a promise of a deleted badge item
*/
export function deleteBadge(id) {
	if (isLoggedIn()) {
		return axios.delete(`/v1/badges/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Creates a badge
* @returns {Promise} response - a promise of a category to be created
*/
export function createBadge(data) {
	if (isLoggedIn()) {
		return axios.post('/v1/badges', data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}


/**
* Updates a badge
* @param {String} id - category Id
* @param {String} data - Category data object
* @returns {Promise} response - a promise of a category to be updated
*/
export function updateBadge(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/badges/${id}`, data);
	}
	
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}
