<template>
	<div>
		<template v-if="loadingBadge">
			<div class="loading">
				<Loader />
			</div>
		</template>

		<template v-else>
			<section class="main-grid__row user-profile">
				<div class="user-profile__card">
					<div class="user-profile__avatar">
						<img
							v-if="itemData.avatar"
							:src="itemData.avatar"
							alt="Profile picture">
						<img
							v-else
							src="../../assets/img/user-profile.png" alt="User profile picture">
					</div>
					<div class="user-profile__primary">
						<h3 class="user-profile__name">Badge Detail Section</h3>
					</div>
					<div class="user-profile__item">
						<h5 class="user-profile__section__title">Badge Name:</h5>
						<p>{{ itemData.name }}</p>
					</div>
					<div v-if="getLoggedUser.category === 'administrator'">
						<button @click="deleteBadge" class="btn btn__danger">Delete</button>
						<Loader v-if="deletingBadge" />
					</div>
				</div>
				<div class="user-profile__tabs">
					<ul class="nav nav-tabs" id="myTab" role="tablist">
						<li class="nav-item">
							<a class="nav-link active" id="activity-tab" data-toggle="tab" href="#activity" role="tab" aria-controls="activity" aria-selected="false">Edit Badge</a>
						</li>
					</ul>
					<div class="tab-pane fade show active user-profile__tabs__section" id="activity" role="tabpanel" aria-labelledby="activity-tab">
						<div class="settings">
							<form
								method="post"
								@submit.prevent="updateBadge">
								<div class="settings__primary">
									<div class="settings__avatar">
										<img
											v-if="itemData.avatar && itemData.avatar !== 'fake.jpg'"
											:src="itemData.avatar"
											alt="Profile picture">
										<img
											v-else
											src="../../assets/img/user-profile.png"
											alt="user avatar">
										<span class="icon icon--edit">
											<label class="avatar__label" for="upload-image">
												<input
													ref="avatar"
													class="avatar__input"
													id="upload-image"
													type="file"
													@change="changeAvatar"
													accept="image/*">
												<svg width="9" height="9" fill="none" xmlns="http://www.w3.org/2000/svg">
												<path fill="none" d="M0 0h9v9H0z"/>
												<path d="M1.125 6.468v1.407h1.406L6.68 3.727 5.272 2.321 1.125 6.468zM7.766 2.64a.373.373 0 0 0 0-.529l-.877-.878a.373.373 0 0 0-.529 0l-.686.687L7.08 3.326l.686-.686z" fill="#fff"/>
												</svg>
											</label>
										</span>
										<div class="avatar__loader">
											<loader v-if="loading" size="small" />
										</div>
									</div>
								</div>
								<div class="invalid-feedback">
									{{ errors.avatar }}
								</div>
								<div class="settings__field">
									<div class="form-row">
										<div class="form-group col-md-6">
											<div class="settings__value">
												<label for="jobTitle">Name</label>
												<input
													:class="[{'is-invalid': errors.name}]"
													@keydown="clearErrors($event.target.name)"
													v-model="itemData.name"
													name="title"
													value=""
													type="text"
													class="form-control"
													id="jobTitle">
											</div>
											<div class="invalid-feedback">
												{{ errors.name }}
											</div>
										</div>
										<div class="form-group col-md-6">
											<div class="settings__value">
												<label for="badge">Value</label>
												<input 
													type="number" 
													:class="[{'is-invalid': errors.value}]"
													class="form-control" 
													id="badge" 
													placeholder="Badge Value"
													@keydown="clearErrors($event.target.name)"
													v-model.trim="itemData.value">
												<div class="invalid-feedback">
													{{ errors.value }}
												</div>
											</div>
										</div>
									</div>
									<div class="form-row">
										<div class="form-group col-md-6">
											<div class="settings__value">
												<label for="action">Select an action</label>
												<select 
													class="custom-select form-control"  
													id="category"
													:class="[{'is-invalid': errors.action}]"
													v-model="itemData.action">
													<option value="exams">Exams</option>
													<option value="forum">Forum</option>
												</select>
												<div class="invalid-feedback">
													{{ errors.action }}
												</div>
											</div>
										</div>
										<div class="form-group col-md-6">
											<div class="settings__value">
												<label for="action">Select a criteria</label>
												<select 
													class="custom-select form-control"  
													id="category"
													:class="[{'is-invalid': errors.criteria}]"
													v-model="itemData.criteria">
													<option value=">">Gretaer Than</option>
													<option value="<">Less Than</option>
													<option value="=">Equal To</option>
												</select>
												<div class="invalid-feedback">
													{{ errors.criteria }}
												</div>
											</div>
										</div>
									</div>
								</div>
								<div class="settings__primary">
									<button
										class="btn btn__save"
										type="button"
										name="button"
										@click.prevent="updateBadge"
										v-bind="{'disabled': loading}">
										Save
									</button>
									<Loader  v-if="loading"/>
								</div>
							</form>
						</div>
					</div>
				</div>
			</section>
		</template>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import { setTimeout } from 'timers';
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import { updateBadge, getBadge, deleteBadge } from '../../utils/api/badge';
import { success, fail } from '../../utils/toast';
import badge from '../../utils/validations/badge';

export default {
	name: 'BadgeDetail',
	mixins: [form],
	data() {
		return {
			itemData: {},
			errors: {},
			loadingBadge: true,
			badgeLoaded: false,
			loading: false,
			deletingBadge: false
		};
	},
	components: {
		Loader
	},
	created() {
		this.getBadge();
	},
	methods: {
		deleteBadge() {
			this.deletingBadge = true;
			deleteBadge(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.deletingBadge = false;
						success({
							title: 'Success! Badge has been deleted!'
						});
						setTimeout(() => {
							this.$router.push('/badges');
						}, 500);
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.deletingBadge = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		getBadge() {
			getBadge(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.badgeLoaded = true;
						return true;
					}
					this.badgeLoaded = false;
					return false;
				})
				.catch((error) => {
					this.badgeLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingBadge = false;
				});
			return true;
		},
		updateBadge() {
			const data = this.itemData;
			delete data.created_at;
			delete data.updated_at;
			delete data.id;
			this.validate(data, badge.update);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			updateBadge(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({
							title: 'Success! Badge has been updated!'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		changeAvatar() {
			const img = this.$refs.avatar.files[0];
			const imageSize = 3 * (2 ** 20);
			if (img && img.type.match(/image\/*/)) {
				if (img.size > imageSize) {
					errors.avatar = 'File size greater than 3MB';
					return false;
				}
			}
			const reader = new FileReader();
			reader.onload = (e) => {
				this.itemData.file = e.target.result;
			};
			reader.readAsDataURL(img);
			return true;
		}
	},
	computed: {
		...mapGetters([
			'getLoggedUser'
		])
	}
};
</script>

<style lang="scss">
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.settings {
	&__field {
		margin-top: 30px;
	}
	&__value {
		padding: 0;
		background: #fff;
		input {
		background: #fafafa;
		border: none;
		font-size: 14px;
		height: 36px;
		}
		label {
		font-weight: bold;
		margin-bottom: 0;
		}
	}
}
.avatar {
    &__input {
        display: none;
    }
    &__label {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        margin: 0;
        &:hover {
            cursor: pointer;
        }
    }
    &__loader {
        position: absolute;
        top: 7px;
        right: -45px;
    }
}
</style>
